<template>
    <div class="body">
        <div class="header">
            <van-nav-bar fixed placeholder title="后台管理页面" left-text="" left-arrow @click-left="goBack">
                <template #right>
                    <van-popover v-model="showPopover" trigger="click" :actions="actions" @select="onSelect">
                        <template #reference>
                            <van-button icon="setting-o" type="primary" text="功能选择"></van-button>
                        </template>
                    </van-popover>
                </template>
            </van-nav-bar>
            <van-search @search="searchItem" v-model="value" shape="round" background="#4fc08d" placeholder="请输入搜索关键词" />
        </div>
        <van-divider>商品列表</van-divider>
        <div class="main">
            <div>
                <div v-for="item in items" :key="item.iid">
                    <van-swipe-cell>
                        <van-card :num="item.quantity" :price="item.price" :desc="item.comment" :title="item.name"
                            class="goods-card" :thumb="base + item.img" />
                        <!--  https://img01.yzcdn.cn/vant/ipad.jpeg -->
                        <template #right>
                            <van-button square text="删除" @click="delItem(item)" type="danger" class="delete-button" />
                            <van-button text="分类" @click="PreClassify(item.iid)" type="primary"></van-button>
                        </template>
                    </van-swipe-cell>
                    <br>
                </div>
            </div>
        </div>
        <van-divider>到底啦</van-divider>
        <div class="footer">
            <van-pagination @change="pageChange" v-model="currentPage" :total-items="count" :items-per-page="5"
                :show-page-size="4" force-ellipses />
        </div>
        <!-- 设置物品分类弹出框区域 -->
        <van-popup v-model="flag.classify">
            <div style="width: 15rem; margin: 1rem;">
                <van-radio-group v-model="cid">
                <van-cell-group>
                    <van-cell clickable v-for="cs in classes" :key="cs.cid" :title="cs.classname">
                        <van-radio :name="cs.cid" />
                    </van-cell>
                </van-cell-group>
                </van-radio-group>
                <van-button style="width: 100%;" @click="submitClassify" type="info" round>确定</van-button>
            </div>
        </van-popup>
        <!-- 添加物品弹出框区域 -->
        <van-popup v-model="flag.showAdd">
            <div style="width: 20rem;">
                <van-row>
                    <van-col :span="24">
                        <br>
                        <van-uploader name="uploader" :after-read="onRead">
                            <van-icon name="photograph" />
                        </van-uploader>
                        <br>
                        <van-form @submit="onSubmit">
                            <van-field v-model="add.name" name="商品名称" label="商品名称" placeholder="商品名称"
                                :rules="[{ required: true, message: '请填写商品名称' }]" />
                            <van-field v-model="add.material" label="商品材质"
                                :rules="[{ required: true, message: '请填写商品材质' }]" />
                            <van-field v-model="add.price" type="number" label="商品价格"
                                :rules="[{ required: true, message: '请填写商品价格' }]" />
                            <van-field v-model="add.quantity" type="digit" label="库存数量"
                                :rules="[{ required: true, message: '请填写库存数量' }]" />
                            <van-field v-model="add.comment" type="text" name="备注" label="备注" placeholder="备注"
                                :rules="[{ required: true, message: '请填写商品备注' }]" />
                            <div style="margin: 16px;">
                                <van-button round block type="info" native-type="submit">提交</van-button>
                            </div>
                        </van-form>
                    </van-col>
                </van-row>
            </div>
        </van-popup>
    </div>
</template>

<style>
.body {
    min-height: 100vh;
    background-color: aliceblue;
}

.header {
    z-index: 999;
}

.header .van-button--primary {
    background: none;
    color: black;
    border: none;
}

.main {}

.footer {
    position: fixed;
    bottom: 0%;
    width: 100%;
    z-index: 999;
}

.van-pagination__item {
    background-color: white !important;
}

.van-popover__action {
    width: 7rem;
}

.van-pagination__item--active {
    color: chocolate;
}

.van-button--danger {
    height: 100%;
}
.van-button--primary{
    height: 100%;
}
</style>
<script>
import axios from 'axios';

import { Toast } from 'vant';
console.log(Toast)
const options = {
    data() {
        return {
            flag: { showAdd: false, classify: false },
            value: "",
            currentPage: 1,
            showPopover: false,
            // 通过 actions 属性来定义菜单选项
            actions: [{ text: '新增', icon: 'add-o' }, { text: '类别', icon:"label-o" }, { text: '活动', icon:"gem-o" }],
            add: {
                name: "",
                price: 0.00,
                comment: "",
                quantity: 0,
                material: ""
            },
            iid:"",
            cid:"",
            items: [],
            count: 100,
            file: "",
            classes:[]
        }
    },
    computed: {
        base() {
            return axios.defaults.baseURL;
        },
    },
    methods: {
        goBack() {
            this.$router.push("/index")
        },
        onSelect(act, index) {
            switch (index) {
                case 0:
                    // 新增物品
                    this.flag.showAdd = true;
                    console.log("新增物品");
                    break;
                case 1:
                    // 进入商品类别管理页面
                    this.$router.push("/admin/itemclass")
                    break;
                case 2:
                    // 功能3
                    this.$router.push("/admin/activity")
                    break;
                default:
                    console.log("非法操作!");
                    break;
            }
        },
        onSubmit() {
            console.log(this.file)
            let form = new FormData();
            form.set("image", this.file.file);
            // form.set("item",{
            //     name: this.add.name,
            //     price: this.add.price,
            //     material: this.add.material,
            //     quantity: this.add.quantity,
            //     comment: this.add.comment,
            // })
            form.set("name", this.add.name);
            form.set("price", this.add.price);
            form.set("material", this.add.material);
            form.set("quantity", this.add.quantity);
            form.set("comment", this.add.comment);

            axios.post("/admin/add", form).then((resp) => {
                Toast(resp.data.msg);
                if (resp.data.data) {
                    this.pageChange();
                    this.itemsCount();
                }
            });
            this.flag.showAdd = false;

            // 重置表单
            this.add.name = "";
            this.add.price = 0.00;
            this.add.comment = "";
            this.add.quantity = 0;
            this.add.material = "";

        },
        getItemClasses(){
            axios.get("/admin/itemclasses").then((resp)=>{
                this.classes = resp.data.data;
            });
        },
        // 设置物品分类前置函数, 显示弹出框
        PreClassify(iid){
            this.getItemClasses();
            this.flag.classify = true;
            // console.log(iid);
            this.iid = iid;
        },
        submitClassify(){
            console.log(this.iid,this.cid)
            axios.post("/admin/addic",{iid:this.iid, cid:this.cid}).then((resp)=>{
                Toast(resp.data.msg);
            });
            this.cid = "";
        },
        pageChange() {
            console.log(this.currentPage);
            axios.get(`/items/${this.currentPage}/${5}`).then((resp) => {
                this.items = resp.data.data;
                // Toast(resp.data.msg);
            });
        },
        itemsCount() {
            axios.get("/items/count").then((resp) => {
                this.count = parseInt(resp.data.data);
            });
        },
        delItem(item) { // del item by iid
            // console.log(item);
            axios.delete(`/admin/${item.iid}`).then((resp) => {
                Toast(resp.data.msg)
                if (resp.data.data) {
                    this.pageChange();
                    this.itemsCount();
                }
            });
        },
        onRead(file) {
            this.file = file;
        },
        searchItem() {
            let f = new FormData();
            f.set("key", this.value);
            axios.post(`/admin/search`, f).then((resp) => {
                this.items = resp.data.data;
            });
        }
    },
    mounted() {
        this.itemsCount();
        this.pageChange();
    }
};

export default options;
</script>